<template>
  <div>
    <header class="mui-bar mui-bar-nav">
      <h1 class="mui-title">用户登录</h1>
    </header>
    <div class="mui-content" style="margin-top:3px;">
      <form class="mui-input-group">
        <div class="mui-input-row">
            <label>手机号码</label>
            <input type="text" id="phone" class="mui-input-clear" :value="userPhones" readonly/>
            <button type="button" id="upBtn" class="mui-btn mui-btn-primary" @click="updataUserPhone">修改</button>
        </div>
        <div class="mui-input-row">
            <label>验证码</label>
            <input type="text" id="tel" class="mui-input-clear" v-model="codeMsg"  placeholder="请输入验证码" />
            <button type="button" id="vercode" class="mui-btn mui-btn-primary mui-btn-outlined" @click="getImageMessage">{{countDown}}</button>
        </div>
        <div class="mui-button-row">
                <button type="button" id="loginBtn" data-loading-icon="mui-spinner mui-spinner-custom" data-loading-text="登录中..."
                class="mui-btn mui-btn-primary" @click="userLogin">登录</button>
            </div>
      </form>
    </div>
  </div>
</template>
<script>
  export default {
    name: "userPhoneLogin",
    data() {
      return {
        userPhones: "",
        codeMsg: "",
        userCode: "",
        userTypeCode: "",
        userLoginCode: "",
        isTestIndex: true,
        // codeTime: "",
        userByCode:"",
        countDown:'获取验证码',
        timeCount: 60,
        canClick:true,
        paramData: this.$route.params.idTypeList
      }
    },
    methods: {
      getImageMessage() {
        if(!this.canClick) return
        if (this.userPhones) {
          this.$http.post("/sendVercode", {
            userCode: this.userCode,
            userPhones: this.$store.getters.user.userPhones
          }).then(
            (response) => {
              let type = response.data.status;
              if (type) {
                if (type == 'success') {
                  this.userByCode = response.data.userByCode
                  this.mui.toast('验证码已发送',{duration:'short',type:'div'})
                } else {
                  alert(type)
                }
              }
            },
            (error) => {
              console.log(error);
            }
          )
        }
        this.canClick = false
        this.countDown = this.timeCount+'s后重新发送'
        let clock = window.setInterval(() => {
          this.timeCount--
          this.countDown = this.timeCount + 's后重新发送'
          if(this.timeCount < 0){
            window.clearInterval(clock)
            this.countDown = '获取验证码'
            this.timeCount = 60
            this.canClick =  true
          }
        },1000)
      },
      userLogin: function() {
        if(!this.codeMsg){
          this.mui.alert("请输入验证码")
          return;
        } else {
          this.$http.post("/checkedMessage", {
            userCode: this.userCode,
            userByCode: this.userByCode,
            codeMsg: this.codeMsg,
            userPhones: this.$store.getters.user.userPhones
          }).then(
            (response) => {
              let type = response.data.status;
              console.log(response.data.msg)
              if (type) {
                if (type == 'success') {
                  this.$router.push({
                    name: 'Home',
                    query:{
                      userCode:this.userCode
                    }
                  });
                } else {
                  alert(response.data.msg)
                }
              }
            },
            (error) => {
              console.log(error);
            }
          )
        } 
      },
      updataUserPhone(){
          if (this.userPhones) {
            //this.$store.commit("LocalUser",{idTypeList:this.idTypeList});
            //this.$router.push('/updataUserPhone');
            this.$router.push({
              name: 'UpdataUserPhone',
              params: {
                idTypeList:this.paramData
              }
          });
          }
      }
    },
    created: function () {
      this.userPhones = this.$store.getters.user.userPhones;
      this.userPhones = this.userPhones.substr(0,3)+'****'+this.userPhones.substr(7,4);
      this.userCode = this.$store.getters.user.userCode;
      this.userTypeCode = this.$store.getters.user.userTypeCode;
      this.userLoginCode = this.$store.getters.user.userLoginCode;
    }
  }
</script>

<style scoped>
#tel{
  width:32%;
  float: left;
}
#vercode{
  float: left;
  width: 30%;
  /* margin-top: 7.3px; */
  border: none;
}
#phone{
  width: 35%;
  float: left;
}
#upBtn{
  line-height: 0.8;
  width: 20%;
  margin-top: 5px;
  margin-right: 10px;
}
</style>



